.sidebar {
    flex: 1;
    height: calc(100vh - 50px);
    background-color: #264F73;
    position: sticky;
    top: 50px;
}

.sidebarWrapper {
    padding: 20px;
    color: rgb(160, 160, 160);
}

.sidebarMenu {
    margin-bottom: 10px;
}

.sidebarTitle {
    font-size: 13px;
    color: rgb(187, 187, 187);
}

.sidebarList {
    list-style: none;
    padding: 5px;
}

.sidebarListItem {
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
    background-color: #30597e;
}

.sidebarIcon {
    margin-right: 5px;
    font-size: 20px !important;
}